import React, { Component } from "react";
import Web3 from "web3";
import "./App.css";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import {
  TODO_LIST_ABI,
  TODO_LIST_ADDRESS,
  TODO_ENTER_YOUR_KEY
} from "./config";

class App extends Component {
  async UNSAFE_componentWillMount() {
    await this.loadWeb3();
    await this.loadBlockchainData();
  }

  async loadWeb3() {
    if (window.ethereum) {
      console.log("11");
      window.web3 = new Web3(window.ethereum);
      await window.ethereum.enable();
    } else if (window.web3) {
      console.log("22");
      window.web3 = new Web3(window.web3.currentProvider);
    } else {
      console.log("33");
      window.alert("No ethereum broswer detected! You can check out MetaMask!");
    }
  }

  async loadBlockchainData() {
    const web3 = new Web3(
      Web3.givenProvider || "https://goerli.infura.io/v3/" + TODO_ENTER_YOUR_KEY
    );

    console.log("https://goerli.infura.io/v3/" + TODO_ENTER_YOUR_KEY);

    const MyDeposit = new web3.eth.Contract(TODO_LIST_ABI, TODO_LIST_ADDRESS);
    console.log(MyDeposit);

    //call de l'account metamask
    const accounts = [await web3.eth.getAccounts()];
    this.setState({ account: accounts[0] });
    console.log("44");

    // call du owner
    const owners = [await MyDeposit.methods.owner().call()];
    this.setState({ owner: owners[0] });
    console.log("this.state.owner : " + this.state.owner);
    console.log("55");

    //call de la balance
    const balances = [await MyDeposit.methods.getBalance().call()];
    console.log(balances[0]);
    this.setState({ balance: balances[0] });
    console.log("this.state.balance : " + this.state.balance);

    const address = "0x3B8F16325799ce799555243418df22C5c8e81f48";
    this.setState({ expeditor: address[0] });

    // send ether to contract
    ///await MyDeposit.methods.transferEther(this.state.owner, [10]).send({
    ///  from: "0x3B8F16325799ce799555243418df22C5c8e81f48",
    ///  gas: 3000000
    ///});

    // transfert ether to contract,

    // await MyDeposit.methods.sendEther().send({from: address , value: 10});
  }

  constructor(props) {
    super(props);
    this.state = {
      account: [],
      balance: [],
      owner: [],
      expeditor: ""
    };
  }

  render() {
    return (
      <div>
        <header>
          <div id="loader" className="text-center">
            <h1 className="text-center">
              Connect Metamask and Access a smart contract
            </h1>
          </div>
        </header>

        <body>
          <div className="container-fluid">
            <div className="row">
              <Container>
                <Row>
                  <Col>
                    <p className="text-center ">Metamask informations</p>
                  </Col>
                  <Col>
                    <p>
                      {" "}
                      Metamask connected account is : {this.state.account}{" "}
                    </p>
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <div id="loader" className="text-center">
                      <p className="text-center ">Contract informations</p>
                    </div>
                  </Col>
                  <Col>
                    <div id="content">
                      <p> Contract balance is : {this.state.balance} </p>
                      <p> Contract owner is : {this.state.owner} </p>
                    </div>
                  </Col>
                </Row>
              </Container>
            </div>
          </div>
        </body>
      </div>
    );
  }
}

export default App;

